  position: relative;
  width: 100%;
}
.container .buttons {
  position: absolute;
  top: 80%;
  left: 10%;
  cursor: pointer;
  border-radius: 5px;
  margin: auto;
  display: none;
}
.container:hover .buttons {
  display: inline-block;
}



























































































































.remove-tag {
  color: red;
  cursor: pointer;
}





























































































